import React, { useEffect, useState } from 'react'
//import background from "../assets/images/background.jpg"
import usericon from "../assets/images/user-icon.png"
import ReactStars from 'react-stars'
import Header from '../components/Header/Header'
import { Link, useParams } from 'react-router-dom'
import axios from 'axios'
import Cookiesservices from '../Services/Cookiesservices'

const Service = () => {
        const{id}=useParams();
        const token=Cookiesservices.get('jwt')
        const [data , setdata]=useState([])
useEffect(()=>{
        const service=async()=>{
                const response=await axios.get(`https://creen-program.com/api/services/show/${id}`,{
                        headers:{
                                Authorization:`Bearer ${token}`
                        }
                })
                setdata(response.data.data.trainers)
        }
        service()
},[id, token])
  return (
   <>
   <Header/>
   <div className="cards" style={{display: "grid",
        gridTemplateColumns: "repeat(2,1fr)",
        rowGap: "8px",
        columnGap: "5px",
        height: "100%",
        width: "100%"}}>
        {
data.length===0?<p style={{    position: "relative",
        left: "64%",
        color: "#000",
        top: "50%",
        width: "100%",
        height: "100%",
        fontSize: "22px",
        fontWeight: "bold"}}>لا يوجد مدربين</p>:
data.map((item , index)=>(
        <div className="card" key={index} style={{    width: "150px",
borderRadius: "10px",
height: "200px",
position: "relative",
alignItems: "center"}}>
        <img src={item.user.cover} alt="" style={{height:"60px",borderTopLeftRadius:"10px",borderTopRightRadius:"10px"}} />
        <div className="info-user" style={{display: "flex",
flexDirection: "column",
alignItems: "center",
position: "absolute",
bottom: "3px"}}>
        <img src={item.user.profile?item.user.profile:usericon} alt="" style={{width:"50px",height:"50px",borderRadius:"50%"}} />
        <span>{item.name}</span>
        <span>{item.joptitle}</span>
        <ReactStars
count={5} 
value={item.user.num_of_rating}
size={24}
color={'#ffd700'} />
        <Link to={`/services/trainer/show/${item.id}`} style={{textDecoration:"none",width:"100%"}}><button style={{border: "1px solid green",
background: "transparent",
width: "100%",
borderRadius: "16px",marginBottom:"5px"}} >شاهد</button ></Link>
        </div>
    </div>
))
        }
       

    </div>
   </>
  )
}

export default Service