import React  from 'react'
import Header from '../components/Header/Header'
import "../styles/Wallet.css"
import LinksHome from '../components/links-home/LinksHome'
import ChatHome from '../components/chat-home/ChatHome'
import user_icon from "../assets/images/user-icon.png"
import { Link } from 'react-router-dom'
import creen from "../assets/images/ic_launcher.png"
import "../styles/charge.css"
const Wallet = () => {


  return (
    <>
    <Header/>
<section style={{width:"100%",display:'flex',justifyContent:"space-between"}}> 
    <ChatHome/>
<section style={{width:"100%"}}>
<div class="container " style={{padding:"0px",margin:"0px"}}>
        <div className='wallet-bg'>
            <div class="d-flex align-items-center pt-5" style={{    flexDirection: "row-reverse"}}>
                <img src={user_icon} class="rounded-circle mx-3" style={{width:"50px",height:"50px"}} alt=""/>
                <h1 class="text-light">احمد محمد جمعة</h1>
            </div>
            <div class="d-flex justify-content-between align-items-center" style={{    flexDirection: "row-reverse",textAlign: "end"}}>
                <p class="mx-3 my-4 text-light" style={{fontSize: "29px"}}>الرصيد المتوفر
                    <span class="d-block" style={{fontWeight: "bold",direction: "rtl",textAlign: "right"}}>5000 SAR</span>
                </p>
                <img src={creen}  alt="" style={{width:"120px"}}/>
            </div>
        </div>
        <div class="wrapper">
            <div class="app-wrapper">
                <div class="wallet-wrapper">
                    <div class="cards">
                        <div class="cards" style={{display:"flex",flexDirection:"row-reverse"}}>
                            <div class="credit-card visa active">
                                <Link to={"/charge"} style={{textDecoration:"none",color:"#000"}}>
                                    <div class="card-image">
                                    
                                    <h2>شحن</h2>
                            </div></Link>
                            </div>
                            <div class="credit-card amex">
                                <Link to={"/Winderthrow"} style={{textDecoration:"none",color:"#000"}}>
                                <div class="card-image">
                                    <h2>سحب</h2>
                                </div>
                                </Link>
                            </div>
                            <div class="credit-card mc">
                                <Link style={{textDecoration:"none",color:"#000"}}>
                                <div class="card-image">
                                    <h2>النقاط</h2>
                                </div></Link>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="transactions-wrapper" style={{marginTop:"-35px"}}>
                    <div class="row row1 ">
                <h3 style={{fontSize:"30px"}}>رصيد المحفظة</h3>
                        <p class="text-center" style={{color:"#fff",fontSize:"30px"}}>5000 SAR</p>
                    </div>
                    <div class="row row2">
                        <h3 style={{    color: "#03767d",fontSize:"30px"}}>المشتريات </h3>
                        <p class="text-center" style={{    color:" #03767d",fontSize:"30px"}}>4000 SAR</p>
                    </div>
                    <div class="row row3">
                        <h3 style={{fontSize:"30px",    color: "#03767d"}}>المبيعات </h3>
                        <p class="text-center" style={{    color: "#03767d",fontSize:"30px"}}>2600 SAR</p>
                    </div>
                </div>
            </div>
        </div>
    </div>

    </section>
    <LinksHome/>
</section>
    </>

    )
}

export default Wallet