import React from 'react'
import Links from '../../assets/data/Links'
import { Link } from 'react-router-dom'
import { motion } from 'framer-motion'
import "../links-home/Links.css"


const LinksHome = () => {
  return (
    <section className='links_home'>
    {
      Links.map((link , index)=>(
        
        <div className="link_home" style={{display:"flex",alignItems:"center" , gap:"2px"}} key={index} ><Link  to={link.src} key={link.id}><motion.span whileTap={{scale:1.2}}>{link.name}</motion.span></Link>
        <img src={link.imgUrl} alt="" style={{width:"30px",height:"30px"}} /></div>
      ))
    }
  </section>
  )
}

export default LinksHome