import React from 'react'
import Header from '../components/Header/Header'
import { Link } from 'react-router-dom'
import { useDispatch ,  useSelector } from 'react-redux'
import { cartActions } from '../redux/slices/cartSlice'
import { motion } from 'framer-motion'


const Cart = () => {

  const cartItems=useSelector(state=>state.cart.cartItems)
  const totalAmount=useSelector(state=>state.cart.totalAmount) 
   return (
<>
<Header/>
<div style={{display:"flex",background:"#fff",height:"100vh",padding:"0px 20px"}}>{
  cartItems.length === 0?   <p style={{    color: "#000",
  fontSize: "x-large",width:"80%",textAlign:"center"}}>No item added to the cart</p>:
  
  
    <div className="cart" style={{background:"#fff",height:"100vh",width:"100%"}}>
  <div className="container" style={{display: "flex",
    justifyContent: "center",
    alignItems: "center"}}>
    <div className="productss" style={{    width: "80%",
    textAlign: "center"}}>
      <table className='table bordered'>
       <thead>
                  <tr>
                    <th>Image</th>
                    <th>Title</th>
                    <th>Price</th>
                    <th>Qty</th>
                    <th>Delete</th>
                  </tr>
                </thead>
                <tbody>
                 {
                  cartItems.map((item , index)=>(
                    <Tr item={item} key={index} />
                  ))
                 }
                                 </tbody>
              </table>

    
    </div>
   
  </div>
</div>


}
<div className="price" style={{    width: "20%"}}>
    <div>
                <h6  style={{display:"flex",alignItems:"center",justifyContent:"space-between"}}>
                  Subtotal 
                <span  style={{    fontWeight: "bold",
    fontSize: "calc(1.275rem + 0.3vw) !important"}}>${totalAmount}</span>
                </h6>
              </div>
              <p style={{marginTop:"5px"}}>taxes and shipping will calculate in checkout</p>
              <div>
              <button style={{width: "100%",
    background: "#1e2321",
    border: "none",
    outline: "none",
    padding: "8px 20px",
    borderRadius: "5px",
    cursor:"pointer",
    fontSize: "1rem",
    marginTop: "40px"}}><Link style={{color:"#fff",textDecoration:"none"}} to={''}>Checkout</Link></button>
                <button style={{width: "100%",
    background: "#1e2321",
    border: "none",
    outline: "none",
    padding: "8px 20px",
    borderRadius: "5px",
    cursor:"pointer",
    fontSize: "1rem",
    marginTop: "40px"}}><Link style={{color:"#fff",textDecoration:"none"}} to={'/products'}>Continue Shopping</Link></button>
              </div>
    </div>
</div>
</>
  )
}
const Tr=({item})=>{
  const dispatch = useDispatch()
  const deleteProduct=()=>{
    dispatch(cartActions.deleteItem(item.id))
  }
 return <tr>
                    <td><img src={item.imgUrl} style={{    width: "50px",
    height: "50px",
    borderRadius: "5px"}} alt="" /></td>
                    <td>{item.productName}</td>
                    <td>${item.price}</td>
                    <td>{item.quantity}px</td>
                    <td><motion.i style={{cursor:"pointer"}} whileTap={{scale:1.2}} onClick={deleteProduct} class="ri-delete-bin-line"></motion.i></td>
                  </tr>


}
export default Cart