import React, { useState , useEffect } from 'react'
import ReactStars from 'react-stars'
import Usericon from '../assets/images/user-icon.png'
import { useNavigate, useParams } from 'react-router-dom'
import Header from '../components/Header/Header'
import axios from 'axios'
import Cookiesservices from '../Services/Cookiesservices'
import ChatHome from '../components/chat-home/ChatHome'
import LinksHome from '../components/links-home/LinksHome'
import { Link } from 'react-router-dom'
import User_icon from "../assets/images/user-icon.png"
//import { background } from '@chakra-ui/react'
import threekilo from "../assets/images/threekilo.jpg"
import fivekilo from "../assets/images/fivekilo.jpg"
import eightkilo from "../assets/images/eightkilo.jpg"
import "../styles/trainer.css"
const Trainer = () => {
    const [tab , settab]=useState('')
    const [active , setactive]=useState('');
    const navigate=useNavigate();
    localStorage.setItem('tab',tab)
    const {id}=useParams();
    const[data,setdata]=useState([])
    const token=Cookiesservices.get('jwt')
    const user_id=localStorage.getItem("user_id")
    useEffect(()=>{
        const service=async()=>{
                const response=await axios.get(`https://creen-program.com/api/services/trainer/show/${id}`,{
                        headers:{
                                Authorization:`Bearer ${token}`
                        }
                })
                setdata(response.data.data.trainer)
        }
        service()
},[id, token])
    return (
   <>
   <Header/>
   < section style={{ width: "100%", display: 'flex', justifyContent: "space-between" }}>
        <ChatHome />
        <section className='center' style={{ width: "59%", display: "flex", justifyContent: "space-between", gap: "19px" }}>

        {
        data.length===0?"":
        <div className="trainer" style={{background:"#fff",width:"100%"}}>
        <Link style={{textDecoration:"none",color:"#000"}} to={`/trainer/${data.id}`}>
        <div className="info-user-subs" style={{    display: "flex",
    justifyContent: "flex-end",gap:"35px"}}>
      <div className="information" style={{    display: "flex",
    flexDirection: "column",alignItems:"end"}}>
        <span style={{    fontSize: "23px",
        color: "rgb(102 99 99)"}}>{data.name}</span>
        <span style={{    fontSize: "24px",
        color: "rgb(102 99 99)"}}>{data.joptitle}</span>
        <ReactStars
  count={5} 
value={data.user.num_of_rating}
  size={32}
  color={'#ffd700'} />
      </div>
      <img src={data.user.profile?data.user.profile:Usericon} alt="" style={{width:"60px",height:"60px",borderRadius:"50%"}} />
    </div>
        </Link>
    <div className="content" style={{    display: "flex",
    flexDirection: "column"}}>
        <p style={{    textAlign: "end",
    color: "#231f1f",
    height: "97px",
    overflow: "hidden"}}>{data.description}</p>
        <span style={{direction: "rtl",
    color: "green",
    marginTop: "-15px",
    cursor: "pointer"}}>المزيد...</span>
    </div>
    <div className="packges" style={{    textAlign: "center",
    marginRight: "9px",
    marginTop: "16px"}}>
        <p style={{display: "flex",
    flexDirection: "row",
    justifyContent: "center",
    color: "#000",
    fontWeight: "bold",
    gap: "5px"}}>اشتري باقتك المفضلة<span style={{background: "red",
        padding: "10px",
        color: "white",
        borderRadius: "2px",
        marginTop: "-13px"}}>الان</span></p>
        
        <p style={{color: "#000",
    fontWeight: "bold",
    background: "yellow",
    borderRadius: "5px",
    width: "75%",
    display: "flex",
    justifyContent: "center",
    margin: "auto"}}>الباقات الشهرية المميزة</p>
    </div>
    <div className="packges-buttons"  style={{marginTop: "10px",
    display: "flex",
    justifyContent: "space-around",
    flexDirection: "row-reverse"}}>
        <button onClick={()=>settab('التميز')} style={{display: "flex",
    flexDirection: "column",
    background: "transparent",
    border: "none",
    alignItems: "center",
    width: "100%"}}>
            <span>التميز</span>
            <span style={{ background: "orange",
    padding: "0px 20px",
    borderRadius: "5px",
    width: "60%",
    display: "flex",
    justifyContent: "center",
    alignItems: "center"}}>50</span>
        </button>
        <button onClick={()=>settab('برو')} style={{display: "flex",
    flexDirection: "column",
    background: "transparent",
    border: "none",
    alignItems: "center",
    width: "100%"}}>
            <span>برو</span>
            <span style={{ background: "orange",
    padding: "0px 20px",
    borderRadius: "5px",
    width: "60%",
    display: "flex",
    justifyContent: "center",
    alignItems: "center"}}>100</span>
        </button>
        <button onClick={()=>settab('ماستر')} style={{display: "flex",
    flexDirection: "column",
    background: "transparent",
    border: "none",
    alignItems: "center",
    width: "100%"}}>
            <span>ماستر</span>
            <span style={{ background: "orange",
    padding: "0px 20px",
    borderRadius: "5px",
    width: "60%",
    display: "flex",
    justifyContent: "center",
    alignItems: "center"}}>150</span>
        </button>
            
    </div>
    <div className="kilo" style={{    display: "flex",
    justifyContent: "center",gap:"6px",
    marginTop: "12px",
    flexDirection: "row-reverse"}}>
            <div onClick={()=>settab('التميز')} style={{display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    gap: "2px"}}>
                <span style={{    direction: "rtl"}}>3كيلو</span>
                <img className='fivekilo' src={threekilo} alt="" style={{    width:"90px",
    height: `${tab==="التميز"?"120px":"100px"}`,
    background: "#000",
    borderRadius: "5px",
    border:`${tab==="التميز"?"2px solid":""}`}} />
            </div>
            <div onClick={()=>settab('برو')} style={{display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    gap: "2px"
    }}>
                <span style={{    direction: "rtl"}}>5كيلو</span>
                <img src={fivekilo} className='fivekilo' alt="" style={{    width: "90px",
    height: `${tab==="برو"?"120px":"100px"}`,
    background: "#000",
    borderRadius: "5px",
    border:`${tab==="برو"?"2px solid":""}`}} />
            </div>
            <div  onClick={()=>settab('ماستر')} style={{display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    gap: "2px"}}>
                <span style={{    direction: "rtl"}}>8كيلو</span>
                <img className='fivekilo' src={eightkilo} alt="" style={{    width: "90px",
    height: `${tab==="ماستر"?"120px":"100px"}`,
    background: "#000",
    borderRadius: "5px",
    border:`${tab==="ماستر"?"2px solid":""}`}} />
            </div>
                    </div>
     
    <div className="packages-content" style={{textAlign: "end",
    marginRight: "17px",
    marginTop: "7px",paddingBottom:"1px"}} >
    {
        tab === 'التميز'? <div className='excellence'>
           
        <div className="excellence-content">

            <p style={{color:"#000"}}>مدربة تمارين لايف يوميا</p>
            <p style={{color:"#000"}}>مدربة برنامج تتابع معاك يوميا</p>
            <p style={{color:"#000"}}>اخصائة تغذية معاكم لايف يوميا</p>
            <p style={{color:"#000"}}>شيف ثلاث واجبات يوميا</p>
            <p style={{color:"#000"}}>بالاضافة الي الاشراف الطبي وارشادات طبية لايف يوميا</p>
            
        </div>
    
    </div>:
        tab==='برو'?
        <div className="pro-content">

<p style={{color:"#000"}}>مدربة تمارين لايف يوميا</p>
            <p style={{color:"#000"}}>مدربة برنامج تتابع معاك يوميا</p>
            <p style={{color:"#000"}}>اخصائة تغذية معاكم لايف يوميا</p>
            <p style={{color:"#000"}}>شيف ثلاث واجبات يوميا</p>
            <p style={{color:"#000"}}>بالاضافة الي الاشراف الطبي وارشادات طبية لايف يوميا</p>
            <p style={{color:"#000"}}>استشارات طبية عامة و جلسات خاصة </p>

            <p style={{color:"#000"}}>مرتين في الاسبوع</p>
  
            
        </div>:
        tab==='ماستر'?
        <div className="master-content">
                       <p style={{color:"#000"}}>مدربة تمارين لايف يوميا</p>
            <p style={{color:"#000"}}>مدربة برنامج تتابع معاك يوميا</p>
            <p style={{color:"#000"}}>اخصائة تغذية معاكم لايف يوميا</p>
            <p style={{color:"#000"}}>شيف ثلاث واجبات يوميا</p>
            <p style={{color:"#000"}}>بالاضافة الي الاشراف الطبي وارشادات طبية لايف يوميا</p>
            <p style={{color:"#000"}}>استشارات طبية عامة و جلسات خاصة لايف</p>
            <p style={{color:"#000"}}>مع اضافة افراد العائلة</p>
            <p style={{color:"#000"}}>المشتركة وزوجها والابناء والوالدين</p>
            
        </div>:<></>
    }
       
        <div></div>
        <div></div>
    </div>
    <div className="subscriptionall" style={{display: "flex",
    flexDirection: "column",
    alignItems: "center",
    justifyContent: "center",
    /*padding: "10px"*/
    background: "bisque",
    borderTopRightRadius: "10px",
    borderTopLeftRadius: "10px",
    padding: "22px 0px 77px 0px"
}}>
        <div className="buttons-all" style={{    display: "flex",
    flexDirection: "column",
    alignItems: "end"}}>
        <div className="one" onClick={()=>(setactive('ماستر'))} style={{    display: "flex",
    justifyContent: "center",
    flexDirection: "row-reverse",
    gap: "10px",
    width: "100%",
    background: `${active==="ماستر"?"#fff":""}`,
    borderRadius: "10px",
    padding: "10px 10px"}}>
            <span style={{
                    border: "1px solid #ddd",
                    width: "25px",
                    height: "25px",
                    background: `${active==="ماستر"?"red":""}`,
                    borderRadius: "50%",
                    textAlign: "center",margin:"auto"
            }}><span><i style={{color:`${active==="ماستر"?"#fff":"transparent"}`}} class="ri-check-line"></i></span></span>
            <p style={{    display: "flex",
    gap: "5px",
    color: "#000",margin:"auto",    fontWeight: `${active==="ماستر"?"bold":""}`}}><span style={{    display: "flex",
        flexDirection: "row-reverse",
        gap: "5px"}}>150<span>sar</span></span><span>اشتراك ماستر </span></p>
        </div>
        
        <div className="two" onClick={()=>(setactive('برو'))} style={{    display: "flex",
    justifyContent: "center",
    flexDirection: "row-reverse",
    gap: "10px", padding: "10px 10px",background:`${active==="برو"?"#fff":""}`,borderRadius:"10px"}}>
            <span style={{
                    border: "1px solid #ddd",
                    width: "25px",
                    height: "25px",
                    background: `${active==="برو"?"red":""}`,
                    borderRadius: "50%",
                    textAlign: "center",margin:"auto"
            }}><span><i style={{color:`${active==="برو"?"#fff":"transparent"}`}} class="ri-check-line"></i></span></span>
            <p style={{    display: "flex",
    gap: "5px",
    color: "#000",margin:"auto",fontWeight:`${active==="برو"?"bold":""}`}}><span style={{    display: "flex",
        flexDirection: "row-reverse",
        gap: "5px"}}>100 <span>sar</span></span><span>اشتراك برو</span></p>
        </div>
        
        <div className="three" onClick={()=>{setactive('تميز')}} style={{    display: "flex",
    justifyContent: "center",
    flexDirection: "row-reverse",
    gap: "10px", padding: "10px 10px",background:`${active==="تميز"?"#fff":""}`,borderRadius:"10px"}}>
            <span style={{
                    border: "1px solid #ddd",
                    width: "25px",
                    height: "25px",
                    background: `${active==="تميز"?"red":""}`,
                    borderRadius: "50%",
                    textAlign: "center",margin:"auto"
            }}><span><i style={{color:`${active==="تميز"?"#fff":"transparent"}`}} class="ri-check-line"></i></span></span>
            <p style={{    display: "flex",
    gap: "5px",
    color: "#000",margin:"auto",fontWeight:`${active==='تميز'?"bold":""}`}}><span style={{    display: "flex",
        flexDirection: "row-reverse",
        gap: "5px"}}>50<span>sar</span></span><span>اشتراك تميز</span></p>
        </div>
        </div>
        <button style={{    background: "red",
    border: "none",
    width: "80%",
    padding: "10px",
    borderRadius: "25px",
    color: "#fff",marginTop:"10px"}} onClick={()=>{navigate('/wallet')}}>استمرار</button>
    </div>
    </div>
    }
          

        </section>
        <LinksHome />

      </section>
      <div class="footer-mobile" style={{
        background: "#fff",
        border: "none",
        height: "48px",
        position: "sticky",
        zIndex: "9999",
        bottom: "0", display: "none"
      }}>
        <div class="container">
          <ul style={{
            paddingLeft: "0 !important", display: "flex",
            alignItems: "center",
            justifyContent: "space-between"
          }}>
            <li onClick={()=>{console.log(user_id)}}>

              <img src={User_icon} alt="" style={{ width: "40px", height: "40px", cursor: "pointer" }} />

            </li>
            <li>
            <Link to={"/products"}><span><i style={{ fontSize: "1.9rem", cursor: "pointer", color: "#bcbcbc" }} class="ri-shopping-bag-line"></i></span></Link>  
            </li>
            <li>
            <img src="https://creen-program.com/new/images/m1.png" style={{ width: "1.9rem" }} alt="" />
            </li>
            <li>
              <Link to={"/towatch"} style={{ textDecoration: "none", color: "#fff" }} > <i style={{
                fontSize: "1.7rem",
                color: "#fff",
                cursor: "pointer",
                borderRadius: "10px",
                width: "40px",
                height: "40px",
                textAlign: "center",
                padding: "2px 8px 2px 8px",
                background: "#7d7d7d",
              }} class="ri-play-line"></i></Link>
            </li>
            <li style={{
              color: "rgb(255, 255, 255)",
              cursor: "pointer",
              borderRadius: "50%",
              width: "40px",
              height: "40px",
              textAlign: "center",
              background: "rgb(125, 125, 125)"
            }}>

              <i style={{ fontSize: "1.7rem" }} class="ri-add-line"></i>
            </li>
          </ul>
        </div>
      </div>
   

   
    
   </>
  )
}

export default Trainer