import React , {useEffect , useState , useRef} from 'react'
import ChatHome from '../components/chat-home/ChatHome'
import LinksHome from '../components/links-home/LinksHome'
import "../styles/Towatch.css"
import User_icon from "../assets/images/user-icon.png"
import { Link } from 'react-router-dom'
import Cookiesservices from '../Services/Cookiesservices'
import { motion } from 'framer-motion'
import { toast } from 'react-toastify'
import user_icon from "../assets/images/user-icon.png"
import logo from "../assets/images/ic_launcher.png"
import iconchat from "../assets/images/icon-removebg-preview.png"
import axios from 'axios'


const Towatch = () => {
    const[data,setData]=useState([])
    const [page, setPage] = useState(1);
   
    useEffect(()=>{
      try{
        const fetchData = async () => {
          try {
              const response = await fetch(`https://creen-program.com/api/videos?page=${page}`, {
                  method: 'post',
                  headers: {
                      'Content-type': 'application/json; charset=UTF-8',
                      'accept': 'application/json'
                  },
              });

              if (!response.ok) {
                  throw new Error(`HTTP error! Status: ${response.status}`);
              }

              const newData = await response.json();
              setData((prevData) => [...prevData, ...newData.data.videos.data]);
          } catch (error) {
              console.error('Error fetching data:', error);
          }
      };        
      fetchData()
      }catch(error){}
    },[page])
    const handleScroll = () => {
      if (
          window.innerHeight + document.documentElement.scrollTop + 1 >= document.documentElement.scrollHeight
      ) {
          setPage((prevPage) => prevPage + 1);
      }
  };

  useEffect(() => {
      window.addEventListener('scroll', handleScroll);
      return () => window.removeEventListener('scroll', handleScroll);
  }, []);
 /*const play=(obj)=>{
  if (obj.target.classList.contains("fa-play")) {
    obj.target.previousSibling.play();
    obj.target.previousSibling.setAttribute(
      "controls",
      "controls"
    );
    obj.target.classList.add("hidden");
  } else {
    //obj.target.previousSibling.pause(); 
    document.querySelector(".play-v")
    obj.target.previousSibling.removeAttribute("controls");
    obj.target.classList.add("hidden");
  }

 }*/

 const videoRef = useRef(null);
 const [isPlaying, setIsPlaying] = useState(false);

 const togglePlay = (post_id) => {
   if (isPlaying && post_id) {
     videoRef.current.pause();
   } else {
     videoRef.current.play();
   }
   setIsPlaying(!isPlaying);
 };
 const [tab,settab]=useState('main')
 const menuRef=useRef(null);
 const menuRef_mobile=useRef(null);
 const token=Cookiesservices.get('jwt')
 const handle_logout=()=>{
   Cookiesservices.remove('jwt')
   localStorage.clear()
   toast.success("You have successfully logged out");
 }
 const menuToggle=()=>{
   menuRef.current.classList.toggle('active_menu')
 }
 const menuToggle_mobile=()=>{
   menuRef_mobile.current.classList.toggle('active_menu')
 }
 const handlelikepost = (postId) => {

  setData((prevPosts) =>
    prevPosts.map((post) =>
      post.id === postId
        ? {
          ...post,
          likes_count: post.is_like ? post.likes_count - 1 : post.likes_count + 1,
          is_like: !post.is_like,
        }
        : post
    )
  );
  localStorage.setItem("post_id", postId)
};
const [expand, setexpand] = useState(false)

const Togglercomment = (post_id) => {
  setexpand((prevExpand) => ({
    ...prevExpand,
    [post_id]: !prevExpand[post_id],
  }));
};
const [comment , setcomment]=useState("")
const user_id=localStorage.getItem("user_id")
const fetchcomment = async (post_id_comment) => {

  try {
    if (post_id_comment) {
      const response = await axios.post(
        `https://creen-program.com/api/videos/comment?video_id=${post_id_comment}&comment=${comment}&user_id=${user_id}`,
        {},
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );



      if (response.status === 200) {

        toast.success("created comment successfully")
        window.location.reload()
        setcomment("")
      }

    }
  } catch (error) {
    if (error.response) {
      console.error('Server responded with an error:', error.response.data);
      console.error('Status code:', error.response.status);
    } else if (error.request) {
      console.error('No response received from the server');
    } else {
      console.error('Error setting up the request:', error.message);
    }
  }


}


  return (
    <>
    <>
<header className='header_mobile   to-watch-mobile' style={{display:"none"}} >
      
      <div className="nav_wrapper">
        <div className="icons">
        <div className='profile'> <i class="ri-search-line" style={{fontSize:"1.7rem",color:"#fff"}}></i>  
        </div>
        <span><img src={iconchat} alt="" style={{width: "50px",
    height: "50px",
    borderRadius: "50%",
    color: "#fff"}} /></span>
        <Link to={"/home"}><div className="logo"><motion.img whileTap={{scale:1.2}} src={ /*currentUser? currentUser.photoURL :*/ logo}  alt="" /*onClick={toggleProfileActions}*/ /></div></Link>

        <Link><span ><i class="ri-notification-line"></i></span></Link>
        <Link  className='icon_mobile'><span><i class="ri-function-line"></i></span></Link>


        </div>
        
        

        
      </div>
      <div className='user_nouser' style={{display:"none"}} ref={menuRef_mobile} onClick={menuToggle_mobile} >
       {
        token?<div className="current_user">
          <Link to={"/my_topics"}>موضوعاتي</Link>
          <Link to={"/my_videos"}>فيديوهاتي</Link>
          <Link to="/products">المتجر</Link> 
          <Link to={"/cart"}>السله</Link>
          <Link to={"/services"}>الخدمات</Link>
          
          <Link to='/wallet' style={{display:"flex",alignItems:"center",gap:"10px"}}>محفظتي</Link>
          <Link to="/login" onClick={handle_logout} style={{display:"flex",alignItems:"center",gap:"10px",flexDirection:"row-reverse"}}><span><i class="ri-shut-down-line"></i></span>تسجيل الخروج</Link>
               
        </div>: <div className="no_user">
          <Link to='/signup'>التسجيل</Link>
          <Link to='/login'>تسجيل الدخول</Link>
        </div>
       }
      </div>
  
</header>
<header className='header'>
  
      <div className="nav_wrapper">
        <div className="icons_left">
        <div className='profile'> <motion.img whileTap={{scale:1.2}}  onClick={menuToggle} src={ /*currentUser? currentUser.photoURL :*/ user_icon}  alt="" /*onClick={toggleProfileActions}*/ />

        
        </div>
        <span><i class="ri-live-line"></i></span>
        <span><i class="ri-message-2-line"></i></span>
        <span><i class="ri-play-circle-line"></i></span>
        </div>
        
        
        <div class="search_box">
          <input type="text" placeholder="إبحث عن أعضاء, #هاشتاغ" />
          <span><i class="ri-search-line"></i></span>
          </div>
        
        <div className="icons_right">
          <Link><span className={ `${tab==='add'?'active_tab': ''}  add`} onClick={()=>settab('add')}  ><span className='establishment'>إنشاء</span><i class="ri-add-circle-line"></i></span></Link>
          <Link><span className={ `${tab==='main'?'active_tab': ''}  main`} onClick={()=>settab('main')}><span className='home_page'>الصفحه الرئيسية </span><i class="ri-mail-open-line"></i></span></Link>
          <Link><span ><i class="ri-notification-line"></i></span></Link>
          <Link style={{display:'none'}} className='icon_mobile'><span><i class="ri-align-justify"></i></span></Link>
          <Link to={"/home"}><div className="logo"><motion.img whileTap={{scale:1.2}} src={ /*currentUser? currentUser.photoURL :*/ logo}  alt="" /*onClick={toggleProfileActions}*/ /></div></Link>
          
        </div>
      </div>
      <div className='user_nouser' style={{display:"none"}} ref={menuRef} onClick={menuToggle} >
       {
        token?<div className="current_user">
          <Link to={"/my_topics"}>موضوعاتي</Link>
          <Link to={"/my_videos"}>فيديوهاتي</Link>

          <Link to="/products">المتجر</Link>
          <Link to={"/cart"}>السله</Link>
          <Link to={"/services"}>الخدمات</Link>

          <Link to='/wallet' style={{display:"flex",alignItems:"center",gap:"10px"}}>محفظتي</Link>
          <Link to="/login" onClick={handle_logout} style={{display:"flex",alignItems:"center",gap:"10px",flexDirection:"row-reverse"}}><span><i class="ri-shut-down-line"></i></span>تسجيل الخروج</Link>
        </div>: <div className="no_user">
          <Link to='/signup'>التسجيل</Link>
          <Link to='/login'>تسجيل الدخول</Link>
        </div>
       }
      </div>
  
</header>
</>
    <div className="towatch" style={{display:"flex",justifyContent:"space-between"}}>
        <ChatHome/>
        <section className='section-video' style={{width:"61%",display:"flex",justifyContent:"center",position:"relative",gap:"10px"}}>
          <span className='add-video-video' style={{position: "sticky",
    top: "506px",
    width: "40px",
    height: "40px",
    textAlign: "center",
    marginLeft: "11px"}}><i class="ri-add-line" style={{    color: "#fff",
    fontSize: "1.7rem",
    background: "#062b34",
    padding: "13px",
    borderRadius: "50%"}}></i></span>
                           <div className="all_posts_video" style={{display: "flex",
    flexDirection: "column",
    justifyContent: "center"}}>
                           {
                            data.map((item , index)=>(
                              <div className="posts posts_video" style={{display: "flex",
                                flexDirection: "row-reverse",
                                marginTop: "15px",
                                gap:"20px",position:"relative"}}>
                                   <div className="post post_video" key={index} style={{    width: "66%",
                                background: "#fff",
                                padding: "10px",
                                borderRadius: "8px"}}>
                               
                            
                            <div className='content content_video' style={{    lineBreak: "anywhere",position:"relative"}}>
                             {
             <>
                                     

<video width="100%" ref={videoRef} style={{height:"82vh",background:"#000"}} className='play-video' autoPlay=""    controls >
<source src={item.url} type="video/mp4"/>
</video>
<span onClick={()=>{togglePlay(item.id)}} style={{position:"absolute",zIndex: "9999",
    left: "25%",
    top: "27%",
  }} className='play fa fa-play'   >{!isPlaying?<i class="ri-play-fill" style={{color: " rgb(210 204 204 / 76%)",
  fontSize: "10rem"}}></i>:<></>}</span>
             </>
                             }
                             
                             
                             </div>
                             {
                              expand[item.id] &&
                              <div style={{    position: "absolute",
                              top: "170px",
                              display: "flex",
                              width: "80%",
                              alignItems: "center",
                              flexDirection: "column",
                              background: "#fff",
                              padding: "20px",
                              gap: "17px",
                              left: "65%",
                              transform: "translateX(-50%)",
                              zIndex: "99999999999"}}>
                                 {
                      <> {
                        item.comments_count !== 0 && expand[item.id] &&

                        item.comments.map((item, index) => (

                          <div className="comment" key={index} style={{
                            display: "flex",
                            justifyContent: "space-between",
                            alignItems: "baseline",
                            marginBottom: "15px",
                        
                            width:"100%"
                          }}>
                            <div className="left" style={{ width: "100%" }}>
                              <div className="top" style={{ display: "flex", justifyContent: "space-between " }}>
                                <div className="action"><i class="ri-more-2-line"></i></div>
                                <div className="user_name">{item.user.name}</div>
                              </div>
                              <div className="info" style={{
                                background: "rgb(221 221 221 / 30%)",
                                padding: "15px",
                                textAlign: "end",
                                marginTop: "5px", marginLeft: "15px"
                              }}>

                                <div className="content_comment" style={{ lineBreak: "anywhere" }}>{item.comment}</div>
                              </div>
                            </div>
                            <div className="righ"><img style={{ width: "30px", height: "30px", borderRadius: "50px" }} src={item.user.profile !== null ? item.user.profile : User_icon} alt=''></img></div>
                          </div>

                        ))


                      }

                      </>
                    }
                                                    <div style={{display:"flex",flexDirection:"row-reverse",width:"100%",gap:"5px",alignItems:"center"}}>
                                                    <div className="add_comment" style={{gap:"10px",display: "flex",width: "100%"}}>
                                                      <input style={{width:"100%"}} value={comment} onChange={(e)=>e.target.value?setcomment(e.target.value):""} type="text" placeholder="اكتب تعليق وأضغط أنتر .." />
                                                    </div>
                                                    
                                                    <div className="send_comment" style={{alignItems:"center"}}>
                                                      <span onClick={() => fetchcomment(item.id)}><i style={{fontSize:"1.2rem"}} class="ri-send-plane-2-line"></i></span>
                                                      
                                                    </div>
</div>                                                       </div>
                             }
                            
                                                  </div>
                                                  <div className="post-left post-left-video" style={{display: "flex",
    flexDirection: "column-reverse",
    justifyContent: "center",
    marginTop: "-85px",gap:"10px"}}>
                                                  <div className="header header-video" style={{display:"flex",justifyContent:"space-between",flexDirection:"column",alignItems:"flex-start",gap:"15px"}}>
<div className="left">
  <span className='iconmobile' style={{background: "#ddd",
    padding: "10px",
    borderRadius: "50%",
    width: "40px",
    height: "40px",
    textAlign: "center",
    display: "flex",
    justifyContent: "center",
    alignItems: "center"}}><i class="ri-arrow-down-s-line" style={{fontSize:"1.7rem",    color: "rgb(125, 125, 125)" }}></i></span>
</div>
<div className="right" style={{display: "flex",
alignItems: "flex-start",
gap: "17px",flexDirection:"column-reverse",marginLeft:"5px"}}>
  <div className="left" style={{textAlign:"end",marginRight:"5px"}}>
    <span><b style={{    fontSize: "15px",}}>{item.user.name.slice(0,11)}</b></span>
    <div className="time" style={{    display: "none",
justifyContent: "end",
gap: "16px",
flexDirection: "row-reverse"}}>
      <span style={{direction:"rtl"}}>22ساعة</span>
      <span className='privacy'>
      <i class="ri-global-line"></i>
      <span className='arrow'></span>
      </span>
    </div>
  </div>
  <div className="right">
    <img src={item.user.profile} alt="" style={{    width: "30px",
height: "30px",
borderRadius: "50%"}} />
  </div>
</div>
</div>
<div className="buttons_action" style={{    display: "flex",
    justifyContent: "space-around",
    marginTop: "59px",
    flexDirection: "column-reverse",
    textAlign: "center",
    alignItems:"flex-start",
    gap: "10px"}}>
<span className='internal_share iconmobile' style={{transform: "scaleX(-1)",background: "#ddd",
    padding: "10px",
    borderRadius: "50%",
    width: "40px",
    height: "40px",
    textAlign: "center",
    display: "flex",
    justifyContent: "center",
    alignItems: "center"}}>
  <i class="ri-share-forward-line" style={{transform: "scaleX(-1)",fontSize:"1.7rem", color: "rgb(125, 125, 125)"
}}></i></span>
<span className='retweet iconmobile' style={{background: "#ddd",
    padding: "10px",
    borderRadius: "50%",
    width: "40px",
    height: "40px",
    textAlign: "center",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    flexDirection:"column"}}><i class="ri-repeat-2-line" style={{fontSize:"1.7rem",color: "rgb(125, 125, 125)"}}></i><span style={{fontSize:"14px",color:"green"}}>{item.retweets_count}</span></span>

  <span className='external_share iconmobile' style={{background: "#ddd",
    padding: "10px",
    borderRadius: "50%",
    width: "40px",
    height: "40px",
    textAlign: "center",
    display: "flex",
    justifyContent: "center",
    alignItems: "center"}}><i  class="ri-share-line" style={{fontSize:"1.7rem",color: "rgb(125, 125, 125)"}}></i></span>
<span>
<span onClick={() => { Togglercomment(item.id) }}  className='comment iconmobile'style={{background: "#ddd",
    padding: "10px",
    borderRadius: "50%",
    width: "40px",
    height: "40px",
    textAlign: "center",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    }}>
  <i   class="ri-message-3-line" style={{fontSize:"1.7rem",color: "rgb(125, 125, 125)"}}></i>
  </span>
  
  <span style={{fontSize:"14px",color:"green"}}>{item.comments_count}</span>
</span>
<span>
<span
                        className='heart'
                        onClick={() => handlelikepost(item.id)}
                        style={{background: "#ddd",
    padding: "10px",
    borderRadius: "50%",
    width: "40px",
    height: "40px",
    textAlign: "center",
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    flexDirection:"column"}}                   >
                        {item.is_like ? (
                          <>      <i className="ri-heart-fill" style={{ color: "red", fontSize: "1.7rem", cursor: "pointer" }}></i>

                          </>
                        ) : (
                          <>      <i className="ri-heart-line" style={{ fontSize: "1.7rem", cursor: "pointer", color: "#7d7d7d" }}></i>

                          </>
                        )}

                      </span>
                      <span style={{ fontSize: "14px", color: "green", marginTop: "-8px" }}>{item.likes_count}</span>

</span>
 

</div>
                                                  </div>

                              </div>
                           

                            
                            ))
                          }
                           </div>
          
 
        </section>
        <LinksHome/>
       
            </div>
          
            <div class="footer-mobile towatch-mobile" style={{    background: "#fff",
    border: "none",
    height: "48px",
    position: "fixed",
    zIndex: "999999999",
    bottom: "0px",display:"none",    width: "100%"}}>
        <div class="container">
            <ul style={{paddingLeft:"0 !important",    display: "flex",
    alignItems: "center",
    justifyContent: "space-between"}}>
                <li>
                    
                        <img src={User_icon} onClick={()=>{console.log(user_id)}} alt="" style={{width:"40px",height:"40px",cursor:"pointer"}}/>
                    
                </li>
                <li>
                        <span><i style={{fontSize:"1.9rem",cursor:"pointer",color:"#bcbcbc"}} class="ri-shopping-bag-line"></i></span>
                </li>
                <li style={{    background: "#ddd",
    padding: "5px",
    width: "40px",
    height: "40px",
    borderRadius: "50%",
    textAlign: "center"}}>
               <img src="https://creen-program.com/new/images/m1.png" style={{width:"1.9rem"}} alt="" />
                </li>
                <li>
               <Link to={"/towatch"} style={{textDecoration:"none",color:"#fff"}} > <i  style={{    fontSize: "1.7rem",
    color: "#fff",
    cursor: "pointer",
    borderRadius: "10px",
    width: "40px",
    height: "40px",
    textAlign: "center",
    padding: "2px 8px 2px 8px",
    background: "#7d7d7d",}} class="ri-add-line"></i></Link>
                </li>
                <li>
                    
<Link to={"/home"}  style={{textDecoration:"none",color:"#7d7d7d"}} >                <i  style={{fontSize:"1.9rem",cursor:"pointer",color:"#7d7d7d"}} class="ri-home-4-line"></i>
</Link>        
                </li>
            </ul>
        </div>
    </div>
    </>
  )
}

export default Towatch
