import React , {useEffect , useState} from 'react'
import Header from '../components/Header/Header'
import ChatHome from '../components/chat-home/ChatHome'
import LinksHome from '../components/links-home/LinksHome'
import Cookiesservices from '../Services/Cookiesservices'
import axios from 'axios'
import User_icon from "../assets/images/user-icon.png"
import { toast } from 'react-toastify'

const Mytopics = () => {
    const[data,setData]=useState([])
    const [page, setPage] = useState(1);
   const token=Cookiesservices.get("jwt")
   const user_id = (localStorage.getItem('user_id'));
   const[isclicked , setisclicked]=useState(false)
  const [like , setlike]=useState(null)
  const [post_id , setpost_id]=useState("")
  const [post_id_comment , setpost_id_comment]=useState("")
  const[comment , setcomment]=useState("")
  
  
const fetchcomment=async(post_id_comment)=>{
    setpost_id_comment(post_id_comment)
  
    try {
      if(post_id_comment ){
        const response = await axios.post(
          `https://creen-program.com/api/comments/create?blog_id=${post_id_comment}&comment=${comment}&user_id=${user_id}`,
          {},
          {
            headers: {
              Authorization:`Bearer ${token}`,
            },
          }
        );
      
        
    console.log(response)
    if( response.status === 200){
      setpost_id_comment("")
      toast.success("created comment successfully")
      window.location.reload()
    }
    
      }
        } catch (error) {
          if (error.response) {
            console.error('Server responded with an error:', error.response.data);
            console.error('Status code:', error.response.status);
          } else if (error.request) {
            console.error('No response received from the server');
          } else {
            console.error('Error setting up the request:', error.message);
          }
        }
       
      
  }
const handlelikepost=async(post_id)=>{
    setpost_id(post_id)
    try {
  if(post_id && user_id ){
    const response = await axios.post(
      `https://creen-program.com/api/likes/create?like=${like}&blog_id=${post_id}&user_id=${user_id}`,
      {},
      {
        headers: {
          Authorization:` Bearer ${token}`,
        },
      }
    );
  
    
    
    
  
    // Update like count and clicked state based on the response
    setlike((prev)=>prev + like + (isclicked ? response.data.data  :  response.data.data ++ ));  setisclicked(!isclicked);
    console.log(response)
    
    
  
  
  }
    } catch (error) {
      if (error.response) {
        console.error('Server responded with an error:', error.response.data);
        console.error('Status code:', error.response.status);
      } else if (error.request) {
        console.error('No response received from the server');
      } else {
        console.error('Error setting up the request:', error.message);
      }
    }
  }
  const handelchangecomment=(e)=>{
    if(e.target.value){
      setcomment(e.target.value)
    }else{
      setcomment("")
    }
  }
  
    useEffect(()=>{
        const handlemytopics=async()=>{
      try{
        const response=await axios.post(`https://creen-program.com/api/blogs/user?user_id=${user_id}`,{},{

          headers: {
            'Content-type': 'application/json; charset=UTF-8',
            'accept': 'application/json',
        Authorization:`Bearer ${token}`
        },
      
        })
        if(response.status === 200){
          const response=await axios.post(`https://creen-program.com/api/blogs/user?user_id=${user_id}`,{},{
            headers: {
              'Content-type': 'application/json; charset=UTF-8',
              'accept': 'application/json',
              Authorization:`Bearer ${token}`,

          },
        
          })
          setData((prevData) => [...prevData, ...response.data.data.data]);
          
        }
        
      }catch(error){}
      
        }
        handlemytopics()
      },[page])
      const handleScroll = () => {
        if (
            window.innerHeight + document.documentElement.scrollTop + 1 >= document.documentElement.scrollHeight
        ) {
            setPage((prevPage) => prevPage + 1);
        }
      };
      useEffect(() => {
        window.addEventListener('scroll', handleScroll);
        return () => window.removeEventListener('scroll', handleScroll);
      }, []);
  return (
    <>
    <Header/>
    < section style={{width:"100%",display:'flex',justifyContent:"space-between"}}>
    <ChatHome/>
      <section className='center' style={{width:"59%",display:"flex",justifyContent:"space-between",gap:"5px"}}>
      
     
      <div className="posts" style={{display:"flex",flexDirection:"column",gap:"10px",width:"100%"}}>
      <h2 style={{textAlign: "end",
    background: "#fff",
    padding: "10px",
    borderRadius: "10px",
    marginTop: "7px"}}>موضوعاتي</h2>
                          {
                
                            data.map((item,index)=>(
                              <div className="post" key={index}>
<div className="post_header">
  <div className="left" style={{display: "flex",
    alignItems: "center",
    gap: "10px"}}><span><i class="ri-arrow-down-s-line" style={{fontSize:"1.2rem",cursor:"pointer"}}></i></span> 
</div>
  <div className="right">
    <div className="left">
<div className="user" style={{textAlign:"end"}}>{item.user.name}</div>
<div className="type_category">
  <span>{item.category.name}</span>
  <span style={{direction:"rtl"}}>{item.created_at}</span>
</div>
    </div>
    <div className="right">
      {
        item.user.profile == null ? <img src={User_icon} alt="" style={{    width: "50px",
        height: "50px",
        borderRadius: "50%"}} />:<img src={item.user.profile === null ? User_icon : item.user.profile } alt="" style={{    width: "50px",
        height: "50px",
        borderRadius: "50%"}} />
      }
    </div>
  </div>
</div>
<div className="post_content" style={{textAlign: "end",padding: "10px",display:"flex",flexDirection:"column",gap:"10px"}}>
  {
    item.content !== "" && item.image === "" && item.youtube ==="" ? <span >{item.content}</span>:
     item.content === "" && item.image !== "" && item.youtube === "" ? <img src={item.image} style={{width: "100%",height: "350px",borderRadius:"15px"}} alt=''></img>:
     item.content === "" && item.image === "" && item.youtube !== "" ?  <iframe id="ytplayer" title='youtube'  width="100%" height="340"
     src={item.youtube} frameborder="0"></iframe> :
     item.content !== "" && item.image !== "" && item.youtube === ""?<><img style={{width: "100%",height: "350px",borderRadius:"15px"}} src={item.image} alt=''></img> <span>{item.content}</span></>:
     item.content !== "" && item.image === "" && item.youtube !== "" ?<><iframe id="ytplayer" title='youtube'  width="100%" height="340"
     src={item.youtube} frameborder="0"></iframe>
     <span >{item.content}</span></>:<>""</>
     
  }
</div>
<div className="seen-addfollow" style={{display: "flex",justifyContent: "space-between",alignItems: "center"}}>
<span><span  style={{color:"green"}}>{item.seen}</span><span><i class="ri-eye-line" style={{fontSize:"1.2rem"}}></i></span></span>
</div>
<div className="buttons_action" style={{display:"flex",justifyContent:"space-around"}}>
<span className='internal_share'><i class="ri-share-forward-line" style={{fontSize:"1.4rem",cursor:"pointer"}}></i></span>

<span className='external_share'><i class="ri-share-line" style={{fontSize:"1.4rem",cursor:"pointer"}}></i></span>
                            <span className='comment' style={{display:"flex",flexDirection:"column",alignItems:"center"}}><i class="ri-message-3-line" style={{fontSize:"1.4rem",cursor:"pointer"}}></i><span style={{fontSize:"14px",color:"green"}}>{item.comments_count}</span></span>
                            <span className='retweet' style={{display:"flex",flexDirection:"column",alignItems:"center"}}><i class="ri-repeat-2-line" style={{fontSize:"1.4rem",cursor:"pointer"}}></i><span style={{fontSize:"14px",color:"green"}}>{item.retweets_count}</span></span>

             <span
  className='heart'
  onClick={() => handlelikepost(item.id)}
  style={{ display: "flex", flexDirection: "column", alignItems: "center" }}
>
  {isclicked && item.id === post_id  ? (
    <>      <i className="ri-heart-fill" style={{ color: "red" , fontSize:"1.5rem",cursor:"pointer" }}></i>

      <span style={{ fontSize: "14px", color: "green" }}>{item.likes_count}</span>
    </>
  ) : (
    <>      <i className="ri-heart-line" style={{fontSize:"1.5rem",cursor:"pointer"}}></i>

      <span style={{ fontSize: "14px", color: "green" }}>{item.likes_count}</span>
    </>
  )}
</span>

</div>
{
  item.comments_count !== 0 ? 
 item.comments.map((item , index)=>(
  <div className="comment" key={index} style={{    display: "flex",
    justifyContent: "space-between",
    alignItems: "baseline",
    marginBottom: "15px"}}>
  <div className="left" style={{width:"100%"}}>
    <div className="top" style={{display:"flex",justifyContent:"space-between "}}>
    <div className="action"><span><i class="ri-delete-bin-line"></i></span>
    <span><i class="ri-pencil-line"></i></span></div>
    <div className="user_name">{item.user_name}</div>
    </div>
    <div className="info" style={{background: "#ddd",
    padding: "15px",
    textAlign: "end",
    marginTop: "5px"}}>
      
      <div className="content_comment">{item.comment}</div>
    </div>
  </div>
  <div className="righ"><img style={{width:"50px",height:"50px",borderRadius:"50px"}} src={item.logo !== "" ?item.logo :User_icon} alt=''></img></div>
</div>
 )):<></>
 }
<div className="add_comment" style={{marginTop:"10px"}}>
  <div className="form" style={{display:"flex",alignItems:"center",gap:"10px"}}>
 < input type="text" placeholder="اكتب تعليق وأضغط أنتر .." style={{background:"#ddd"}} value={comment} onChange={handelchangecomment}  />

  <img src={ User_icon} style={{width:"50px",height:"50px",borderRadius:"50%"}} alt="" />
  </div>
  <div className="send" style={{display: "flex",alignItems: "center",gap: "10px"}}>
    <span onClick={()=>fetchcomment(item.id)}><i class="ri-send-plane-2-fill" style={{fontSize:"1.5rem",cursor:"pointer"}}></i></span>
    <span style={{position:"relative",cursor:"pointer"}}><input type="file" style={{    position: "absolute",
    top: "0",
    right: "0",
    minWidth: "100%",
    minHeight: "100%",
    textAlign: "right",
    opacity: "0",
    outline: "0",
    background: "#fff",
    cursor: "pointer",
    display: "block",
    width: "1px"}} /><span><i class="ri-image-fill" style={{fontSize:"1.5rem",cursor:"pointer"}}></i></span></span>
  </div>
</div>
                          </div>
                            ))
                          }
                        </div>
                       
      </section>
      <LinksHome/>
     
    </section>
    </>
  )
}

export default Mytopics